<template>
    <v-container style="width: 100%; " align="center" fluid>
        <v-app-bar
                fixed
                app
                color="#FFFFFF"
                elevation="3"
                class="app-bar-gradient-back"
                dark
        >
            <v-btn dark style="display: inline; padding-left: 0px !important;" text icon
                   @click="$router.go(-1)">
                <v-icon>mdi-arrow-left</v-icon>
            </v-btn>
            <v-spacer></v-spacer>
            <v-toolbar-title>{{filtersTitle}}</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-btn small style="display: inline; height: 100%;" text dark
                   @click="resetSearchingFilters">{{removeFiltersText}}
            </v-btn>

        </v-app-bar>
        <div style="margin: 0px 1% 15px 1%!important;">
            <div style="margin-top: 64px !important"></div>
            <v-row dense v-if="location != null && location != undefined">
                <v-col cols="12">
                    <v-card class="text-center"
                    >
                        <v-card-title class="filter-cards-titles">{{distanceFilterTitle}}</v-card-title>
                        <v-col>
                            <div>
                                <v-slider
                                        v-model="maxDistance"
                                        :tick-labels="maxDistanceRangesText"
                                        :max="3"
                                        step="1"
                                ></v-slider>
                            </div>
                        </v-col>
                    </v-card>
                </v-col>
            </v-row>
            <v-row dense>
                <v-col cols="12">
                    <v-card class="text-center">
                        <v-card-title class="filter-cards-titles" style="padding-bottom: 5px">{{genderFilterTitle}}
                        </v-card-title>
                        <v-card-actions class="justify-center" style="padding-bottom: 0px!important">
                            <v-select
                                    v-model="gender"
                                    :items="genders"
                                    item-value="id"
                                    item-text="option"
                                    return-object
                                    dense
                                    outlined
                            >
                                <template v-slot:item="{item}">
                                    <v-avatar>
                                        <img :src="item.img" style="height: 60%; width: auto"/>
                                    </v-avatar>
                                    {{ item.text }}
                                </template>
                                <template v-slot:selection="{item, select, selected }">
                                    <v-avatar>
                                        <img :src="item.img" style="height: 50%; width: auto"/>
                                    </v-avatar>
                                    {{ item.text }}
                                </template>
                            </v-select>
                        </v-card-actions>
                    </v-card>
                </v-col>
            </v-row>
            <v-row dense>
                <v-col cols="12">
                    <v-card class="text-center">
                        <v-card-title class="filter-cards-titles" style="padding-bottom: 5px">{{ageFilterTitle}}
                        </v-card-title>
                        <v-col>
                            <p v-show="ageRange[1] == 100" align="center" class="primary--text"
                               style="vertical-align: text-bottom; margin-bottom: 5px; vertical-align:bottom"><strong>
                                <span style="font-size: 20px;"><b> </b></span>{{ageRange[0]}} - <span
                                    style="font-size: 20px;"><b>∞</b></span> {{yearsText}}<span
                                    style="font-size: 20px;"><b> </b></span>
                            </strong></p>
                            <p v-show="ageRange[1] != 100" align="center" class="primary--text"
                               style=" vertical-align: text-bottom; margin-bottom: 5px; "><strong>
                                <span style="font-size: 20px;"><b> </b></span>{{ageRange[0]}} - {{ageRange[1]}}
                                {{yearsText}}<span style="font-size: 20px;"><b> </b></span>
                            </strong></p>
                            <div>
                                <v-range-slider
                                        v-model="ageRange"
                                        :max="maxAge"
                                        :min="minAge"
                                        hide-details
                                        class="align-center"
                                >
                                </v-range-slider>
                            </div>
                        </v-col>
                    </v-card>
                </v-col>
            </v-row>

            <v-row dense>
                <v-col cols="12">
                    <v-card class="text-center">
                        <v-card-title class="filter-cards-titles">{{languageFilterTitle}}</v-card-title>
                        <v-card-actions class="justify-center" style="padding: 10px; padding-bottom: 0px">
                            <v-radio-group v-model="languagesRadioButtons" :mandatory="false"
                                           style="margin: 0px!important; min-width:100%!important"
                                           class="radio-group-full-width">
                                <div style="padding: 10px; min-width:100%!important;">
                                    <v-radio class="languageRadioButton" :label="textFilterByLanguageToLearn"
                                             value="languageToLearnRadioButton" color="primary"
                                             style="min-width:100%!important;">
                                        <template v-slot:label style="min-width:100%!important;">
                                            <strong v-bind:class="{'primary--text': languagesRadioButtons == 'languageToLearnRadioButton'}">{{textFilterByLanguageToLearn}}</strong>
                                        </template>
                                    </v-radio>
                                    <v-select
                                            v-model="languageToLearn"
                                            :disabled="languagesRadioButtons != 'languageToLearnRadioButton'"
                                            style="margin-top: 15px"
                                            :items="languages"
                                            item-value="id"
                                            item-text="option"
                                            dense
                                            outlined
                                            return-object
                                    >

                                        <template v-slot:item="{item}">
                                            <v-avatar>
                                                <img :src="createPathToFlagImage(item.id)"
                                                     style="height: 60%; width: auto"/>
                                            </v-avatar>
                                            {{ item.text }}
                                        </template>
                                        <template v-slot:selection="{item, select, selected }">
                                            <v-avatar>
                                                <img :src="createPathToFlagImage(item.id)"
                                                     style="height: 60%; width: auto"/>
                                            </v-avatar>
                                            {{ item.text }}
                                        </template>
                                    </v-select>
                                </div>
                                <div style="padding: 10px; padding-bottom: 0px">
                                    <v-radio class="languageRadioButton" :label="textFilterByKnownLanguage"
                                             value="knownLanguageRadioButton" color="primary">
                                        <template v-slot:label>
                                            <strong v-bind:class="{'primary--text': languagesRadioButtons != 'languageToLearnRadioButton'}">{{textFilterByKnownLanguage}}</strong>
                                        </template>
                                    </v-radio>
                                    <v-select
                                            v-model="knownLanguage"
                                            :disabled="languagesRadioButtons == 'languageToLearnRadioButton'"
                                            style="margin-top: 15px"
                                            :items="languages"
                                            item-value="id"
                                            item-text="option"
                                            dense
                                            outlined
                                            return-object
                                    >

                                        <template v-slot:item="{item}">
                                            <v-avatar>
                                                <img :src="createPathToFlagImage(item.id)"
                                                     style="height: 60%; width: auto"/>
                                            </v-avatar>
                                            {{ item.text }}
                                        </template>
                                        <template v-slot:selection="{item, select, selected }">
                                            <v-avatar>
                                                <img :src="createPathToFlagImage(item.id)"
                                                     style="height: 60%; width: auto"/>
                                            </v-avatar>
                                            {{ item.text }}
                                        </template>
                                    </v-select>
                                </div>
                            </v-radio-group>
                        </v-card-actions>
                    </v-card>
                </v-col>
            </v-row>
            <div style="margin-bottom: 60px"></div>
        </div>
        <v-btn tile large class="bottom-fixed-button" style="display: inline" color="primary" @click="listUsers">
            {{showUsersText}}
        </v-btn>
        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
                style="bottom: 76px"
        >
            {{ snackbarText }}
            <v-btn
                    color="primary"
                    text
                    @click="snackbar = false"
            >
                {{closeText}}
            </v-btn>
        </v-snackbar>
    </v-container>
</template>

<script>
    import Constants from "../../store/Constants";
    import {mapActions, mapMutations} from "vuex";

    // Página de establecimiento de filtros en la búsqueda de usuarios
    // @group Búsqueda
    export default {
        name: "UserProfile",
        data() {
            return {
                genders: [],
                gender: this.$store.getters['users/getGenderFilter'],
                filters: null,
                languages: null,
                languageToLearn: "all",
                knownLanguage: "all",
                knownLanguageRadioButtonDisabled: null,
                languageToLearnRadioButtonDisabled: null,
                maxDistance: this.$store.getters['users/getMaxDistanceFilter'],
                maxDistanceRangesText: this.$store.getters['users/getMaxDistanceRangesText'],
                languagesRadioButtons: "languageToLearnRadioButton",
                slider: 40,
                ageRange: this.$store.getters['users/getAgeRangeFilter'],
                minAge: Constants.MIN_USER_AGE,
                maxAge: Constants.MAX_USER_AGE,
                yearsText: null,
                textFilterByLanguageToLearn: null,
                languagesToLearnTitle: null,
                textFilterByKnownLanguage: null,
                knownLanguagesTitle: null,
                knownLanguageLabel: null,
                languageToLearnLabel: null,
                ageFilterTitle: null,
                distanceFilterTitle: null,
                genderFilterTitle: null,
                languageFilterTitle: null,
                filtersTitle: null,
                removeFiltersText: null,
                toFilterText: null,
                showUsersText: null,
                snackbarText: null,
                snackbar: false,
                closeText: null
            }
        },
        methods: {
            ...mapMutations([
                'setSearchInfoFiltering',
                'resetSearchInfoFiltering',
                'resetSearchInfoUserIDs'
            ]),
            ...mapActions('users', ['setFilters', 'resetFilters']),

            // @vuese
            // Establece los filtros y redirige al usuario a la página de búsqueda de usuarios
            listUsers: function () {
                let self = this;
                let selectedGender;

                if (self.gender.hasOwnProperty("id")) {
                    selectedGender = self.gender.id;
                } else {
                    selectedGender = self.gender;
                }

                let languageFilter = {};

                if (self.languagesRadioButtons === "languageToLearnRadioButton") {
                    languageFilter["filterBy"] = "languageToLearn";

                }
                if (self.languagesRadioButtons === "knownLanguageRadioButton") {
                    languageFilter["filterBy"] = "knownLanguage";
                }

                let selectedLanguageToLearn;
                if (self.languageToLearn.hasOwnProperty("id")) {
                    selectedLanguageToLearn = self.languageToLearn.id;
                } else {
                    selectedLanguageToLearn = self.languageToLearn;
                }

                languageFilter["languageToLearn"] = selectedLanguageToLearn;

                let selectedKnownLanguage;
                if (self.knownLanguage.hasOwnProperty("id")) {
                    selectedKnownLanguage = self.knownLanguage.id;
                } else {
                    selectedKnownLanguage = self.knownLanguage;
                }

                languageFilter["knownLanguage"] = selectedKnownLanguage;

                let resultSetFilters = self.setFilters({
                    gender: selectedGender,
                    ageRange: self.ageRange,
                    language: languageFilter,
                    maxDistance: self.maxDistance,
                    location: self.location
                });

                resultSetFilters.then(() => {
                    this.$router.replace({name: 'searchHome'});
                })
                    .catch((error) => {
                        self.snackbarText = error;
                        self.snackbar = true;
                    });
            },

            // @vuese
            // Elimina los filtros establecidos para la búsqueda de usuarios
            resetSearchingFilters() {
                let self = this;
                let resultResetFilters = self.resetFilters();

                resultResetFilters.then((resetedFilters) => {
                    self.gender = resetedFilters.gender;
                    self.ageRange = resetedFilters.ageRange;
                    if (resetedFilters.language.filterBy == "knownLanguage") {
                        self.languageToLearnRadioButtonDisabled = true;
                        self.knownLanguageRadioButtonDisabled = false;
                    } else {
                        self.languageToLearnRadioButtonDisabled = false;
                        self.knownLanguageRadioButtonDisabled = true;
                    }
                    self.knownLanguage = resetedFilters.language.knownLanguage;
                    self.languageToLearn = resetedFilters.language.languageToLearn;
                    self.maxDistance = resetedFilters.maxDistance;
                })
                    .catch((error) => {
                        self.snackbarText = error;
                        self.snackbar = true;
                    });

            },

            createPathToFlagImage: function (lang) {
                return "../img/flags/" + lang + ".svg";
            },

            // @vuese
            // Obtiene los filtros establecidos para la búsqueda de usuarios
            getFiltersInfo() {
                let self = this;
                let genderOptions = this.$store.getters['user/getGenderOptionsAsArray'];
                genderOptions.forEach(gender => {
                    if (gender.id == "O") {
                        gender.text = this.$i18n.t('messages.TEXT_ANY');
                    }
                    self.genders.push(gender);
                });
                this.genders = genderOptions;
                this.languages = this.$store.getters['user/getLanguageOptionsAsArray'];
                let languageFilter = this.$store.getters['users/getLanguageFilter'];

                if (languageFilter.filterBy == "knownLanguage") {
                    this.languagesRadioButtons = "knownLanguageRadioButton";
                }
                if (languageFilter.filterBy == "languageToLearn") {
                    this.languagesRadioButtons = "languageToLearnRadioButton";
                }

                this.languageToLearn = languageFilter.languageToLearn;
                this.knownLanguage = languageFilter.knownLanguage;
            }
        },
        computed: {
            // @vuese
            // Devuelve la geolocalización del usuario autenticado
            location: function () {
                return this.$store.getters['user/getLocation'];
            }
        },
        created() {
            this.closeText = this.$i18n.t('messages.TEXT_CLOSE');
            this.yearsText = this.$i18n.t('messages.TEXT_SPACE_YEARS');
            this.textFilterByLanguageToLearn = this.$i18n.t('messages.TEXT_FILTER_BY_LANGUAGE_TO_LEARN');
            this.languagesToLearnTitle = this.$i18n.t('messages.TEXT_LANGUAGE_HE_SHE_WANT_TO_PRACTICE');
            this.textFilterByKnownLanguage = this.$i18n.t('messages.TEXT_FILTER_BY_MOTHER_LANGUAGE');
            this.knownLanguagesTitle = this.$i18n.t('messages.TEXT_MOTHER_LANGUAGES');
            this.knownLanguageLabel = this.$i18n.t('messages.TEXT_LANGUAGE_HE_SHE_SPEAK_FLUENTLY');
            this.languageToLearnLabel = this.$i18n.t('messages.TEXT_LANGUAGE_HE_SHE_WANT_TO_IMPROVE');
            this.ageFilterTitle = this.$i18n.t('messages.TEXT_AGE');
            this.distanceFilterTitle = this.$i18n.t('messages.TEXT_DISTANCE');
            this.genderFilterTitle = this.$i18n.t('messages.TEXT_GENDER');
            this.languageFilterTitle = this.$i18n.t('messages.TEXT_LANGUAGE');
            this.filtersTitle = this.$i18n.t('messages.TEXT_FILTERS');
            this.removeFiltersText = this.$i18n.t('messages.TEXT_REMOVE_FILTERS');
            this.toFilterText = this.$i18n.t('messages.TEXT_TO_FILTER');
            this.showUsersText = this.$i18n.t('messages.TEXT_SHOW_USERS');
        },
        beforeMount() {
            this.getFiltersInfo();
        }
    }
</script>

<style scoped>

    .bottom-fixed-button {
        position: fixed;
        bottom: 65px;
        left: 0px;
        width: 100%;
    }

    .radio-group-full-width >>> .v-input__control {
        width: 100%
    }
</style>
